.navbar {
  min-height: 5rem;
  /* margin-top: -5rem; */
  font-size: 1rem;
  position: sticky;
  top: 0;
  z-index: 1000;
  /* background-color: #eaeaea; */
  background-color: #fff;
}

.navbar-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 5rem;
  z-index: 100;
  width: 100%;
  padding: 0 1.5rem;
  max-width: 68.75rem;
}

.navbar-logo {
  justify-self: flex-start;
  cursor: pointer;
  font-size: 2.5vh;
  display: flex;
  align-items: center;
  font-weight: bold;
  text-decoration: none;
  color: #231f20;
}

.mobile-icon {
  display: none;
}

.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
  padding: 0 !important;
  margin: auto;
}

.nav-item {
  height: 5rem;
  margin: auto;
  transition: 0.15s all ease-in-out;
  -webkit-transition: 0.15s all ease-in-out;
}

.nav-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 1.2vh;
  height: 100%;
  cursor: pointer;
  font-size: 2vh;
  color: #231f20;
}

.nav-item:hover {
  border-bottom: 0.2rem solid #d79922;
}

.nav-link:hover {
  color: #d79922;
}

.nav-btn {
  display: flex;
  align-items: center;
}

.nav-btn-link {
  border-radius: 50%;
  white-space: nowrap;
  padding: 0.625rem 1.375rem;
  font-size: 1rem;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s all ease-in-out;
  -webkit-transition: all 0.2s all ease-in-out;
  text-decoration: none;
}

.nav-btn-link:hover {
  transition: all 0.2s all ease-in-out;
  -webkit-transition: all 0.2s all ease-in-out;
}

@media (max-width: 960px) {
  .navbar {
    transition: 0.8s all ease;
  }
}

@media (max-width: 768px) {
  .nav-menu,
  .nav-btn {
    display: none;
  }

  .mobile-icon {
    display: block;
    align-self: center;
    cursor: pointer;
  }

  .nav-link {
    font-size: 2.5vh;
    justify-content: center;
    margin: 0.3rem 0;
  }
}

.logo {
  height: 8vh;
  width: auto;
}
